.App {
  text-align: center;
}

.main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg,  #8CE0EB 0%, #D2CCB1 100%);
  height: 100vh;
}

.mainBlock{
  display: flex;
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0px 4px 18px rgba(115, 146, 108, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
}
